"use client";

import { useState, useEffect } from "react";

const Header = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const sectionList = [
    {
      name: "ABOUT ME",
      href: "#aboutme",
    },
    {
      name: "SKILLS",
      href: "#skills",
    },
    {
      name: "CAREER",
      href: "#career",
    },
    {
      name: "PROJECTS",
      href: "#projects",
    },
    {
      name: "OTHERS",
      href: "#others",
    },
  ];

  useEffect(() => {
    const handleScroll = () => {
      const scrollTop = window.scrollY;
      setIsScrolled(scrollTop > window.innerHeight / 2);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <header id="header" className="w-full fixed top-0">
      {!isScrolled ? (
        <div className="max-w-[1080px] h-16 flex justify-start items-center mx-auto">
          <h1 className="font-bold text-2xl text-black">강찬일 포트폴리오</h1>
          <div className="flex justify-start items-center ml-auto gap-4">
            {sectionList.map((section) => (
              <a href={section.href} key={section.name}>
                {section.name}
              </a>
            ))}
          </div>
        </div>
      ) : (
        <div className="max-w-[1080px] h-16 bg-transparent flex justify-between items-center mx-auto">
          <h1 className="font-bold text-2xl">KCI</h1>
          <div className="relative" onClick={() => setIsMenuOpen(!isMenuOpen)}>
            <div
              className="flex flex-col gap-1 cursor-pointer"
              onClick={() => setIsMenuOpen(!isMenuOpen)}>
              <span className="block w-6 h-1 bg-black"></span>
              <span className="block w-6 h-1 bg-black"></span>
              <span className="block w-6 h-1 bg-black"></span>
            </div>
            <div
              className={`flex flex-col justify-start items-center gap-4 absolute top-[-24px] right-[-16px] w-50 p-4 ${
                isMenuOpen ? "bg-white" : "sr-only"
              }`}>
              <div
                className="relative w-full p-4"
                onClick={() => setIsMenuOpen(!isMenuOpen)}>
                <span className="absolute w-6 h-1 bg-black rotate-45 right-0"></span>
                <span className="absolute w-6 h-1 bg-black -rotate-45 right-0"></span>
              </div>
              {sectionList.map((section) => (
                <a
                  href={section.href}
                  key={section.name}
                  className="text-black text-sm w-full">
                  {section.name}
                </a>
              ))}
            </div>
          </div>
        </div>
      )}
    </header>
  );
};

export default Header;
